import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/view/layout/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: layout,
      redirect:'/news',
      children:[
        {
          path: '/news',
          name: '新闻列表页',
          component: () =>import ('@/view/layout/news/list')
        },
        {
          path: '/news/add',
          name: '新闻列表添加页',
          component: () =>import ('@/view/layout/news/add')
        },
        {
          path: '/news/update',
          name: '新闻编辑页',
          component: () =>import ('@/view/layout/news/add')
        },
        {
          path: '/projects',
          name: '工程列表页',
          component: () =>import ('@/view/layout/projects/list')
        },
        {
          path: '/projects/add',
          name: '工程添加页',
          component: () =>import ('@/view/layout/projects/add')
        },
        {
          path: '/projects/update',
          name: '工程编辑页',
          component: () =>import ('@/view/layout/projects/add')
        }
      ]
    }
  ]
})



