<template>
    <div class="top-header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item v-for="(item,index) in routeList" :to="item.path" :key="index">{{item.name}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
</template>

<script>
    export default {
        name: "top-header",
        data(){
            return{
              routeList:[]
            }
        },
        methods:{
          getList(){
            this.routeList = this.$route.matched.filter(item =>item.name)
          }
        },
        watch:{
          $route() {
            this.getList()
          }
        },
        created(){
          this.getList()
        }
    }
</script>

<style scoped>
.top-header{
  position: fixed;
  left: 210px;
  top: 0;
  right: 0;
  height: 50px;
  padding-top: 25px;
  border-bottom: 1px solid #ccc;
  z-index: 200;
  background: #fff;
}
</style>



