<template>
    <div class="side-bar">
      <el-row class="tac">

        <el-col :span="12">
          <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            background-color="#545c64"
            text-color="#fff"
            :router="true"
            active-text-color="#ffd04b">
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>新闻管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/news">新闻列表</el-menu-item>
                <el-menu-item index="/news/add">添加新闻</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>工程管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/projects">工程列表</el-menu-item>
                <el-menu-item index="/projects/add">添加工程</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-col>
      </el-row>
    </div>
</template>

<script>
    export default {
        name: "side-bar"
    }
</script>

<style scoped>
  .side-bar{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    background:rgb(84,92,100) ;
  }
  .el-col{
    width: 205px;
  }
  .el-menu{
    border: none;
  }
</style>



